
.header {
    padding-top: 45px;
    max-width: 880px;
    margin: 0 auto 40px auto;
    min-height: 74px;
    border-bottom: 1px solid rgb(84, 84, 84, 0.7);
}


@media screen and (max-width: 1023px) {
    .header {
        max-width: 581px;
      }
    }
      
  
  @media screen and (max-width: 767px) {
    .header {
      max-width: 320px;
      min-height: 56px;
      padding-top: 28px;
      margin: 0 auto 42px auto;
    }
  }